<template>
  <div class="mbox">
    <flickity id="flickitybox" ref="flickity" :options="flickityOptions">
      <div class="carousel-cell">
        <div class="container row">
          <div class="col">
            <span class="tnum">01</span>
            <br />
            <span class="title">YT's Heaven 虛擬旅遊網頁</span>
            <br />
            <span class="content">
              於2017年寒假參與「台大資訊工程學系
              HTML5,CSS3,jQuery, Bootstrap網頁設
              計基礎班」課程，並於結訓時完成個人
              網站設計。 網站主題「YT's HEAVEN
              」，介紹天堂三日遊行程，內容純屬虛擬。
              <br />
              <br />#HTML5 #CSS #Bootstrap
            </span>
          </div>
          <div class="col" style="margin:auto">
            <iframe
              width="560"
              height="315"
              src="https://www.youtube.com/embed/vBX5FuFuQxw"
              frameborder="0"
              allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
              allowfullscreen
            ></iframe>
          </div>
        </div>
      </div>
      <div class="carousel-cell">
        <div class="container row">
          <div class="col">
            <span class="tnum">02</span>
            <br />
            <span class="title">iCare 線上心理諮商Bot</span>
            <br />
            <span class="content">
              提供給青少年族群使用的線上諮商工
              具。
              LineBot端是給學生進行註冊、預約與線
              上諮商，而我負責的網頁端是提供諮商
              師查看行事曆、排程、收藏紀錄、線上
              諮商與紀錄個案。
              該專題於「CMoney菁英軟體工程師戰
              鬥營」期末專題發表榮獲第2名。
              <br />
              <br />#HTML5 #CSS #javaScript #Vue.js
              #Bootstrap #jQuery #mongoDB
            </span>
          </div>
          <div class="col" style="margin:auto">
            <iframe
              width="560"
              height="315"
              src="https://www.youtube.com/embed/_-nWJ4Xq04Y"
              frameborder="0"
              allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
              allowfullscreen
            ></iframe>
          </div>
        </div>
      </div>
      <div class="carousel-cell">
        <div class="container row">
          <div class="col">
            <span class="tnum">03</span>
            <br />
            <span class="title">某資訊公司官網</span>
            <br />
            <span class="content">
              為資訊公司開發官網，使用HTML, CSS,
              JavaScript, Bootstrap，框架使用Vue.js。
              <br />
              <br />#HTML5 #CSS #javaScript #Vue.js
              #Bootstrap #jQuery
            </span>
          </div>
          <div class="col" style="margin:auto">
            <img src="../assets/trtech.png" width="535" />
          </div>
        </div>
      </div>
    </flickity>

    <!-- if you don't want to use the buttons Flickity provides -->
    <!-- <button @click="previous()">Custom Previous Button</button>
    <button @click="next()">Custom Next Button</button>-->
  </div>
</template>
<script src="https://unpkg.com/flickity@2/dist/flickity.pkgd.min.js"></script>

<script>
import Flickity from "vue-flickity";

export default {
  components: {
    Flickity
  },
  data() {
    return {
      flickityOptions: {
        initialIndex: 0,
        prevNextButtons: true,
        pageDots: false,
        wrapAround: true

        // any options from Flickity can be used
      }
    };
  },

  methods: {
    next() {
      this.$refs.flickity.next();
    },

    previous() {
      this.$refs.flickity.previous();
    }
  }
};
</script>
<style scoped lang="scss">
* {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
}
#flickitybox {
  margin: 50px 20px;
}
.container {
  margin: 50px auto;
}
img, iframe{
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}
.tnum {
  /* margin: 20px 30px; */
  font-size: 80px;
  /* color: white; */
  font-weight: 800;
}
.title {
  font-size: 25px;
  /* color: white; */
  font-weight: 600;
}
.carousel {
  background: #eee;
}

.carousel-cell {
  width: 100%;
  height: 500px;
  margin-right: 10px;
  /* background: #8c8; */
  counter-increment: gallery-cell;
}

/* cell number */
.carousel-cell:before {
  display: block;
  text-align: left;
  /* content: counter(gallery-cell); */
  /* line-height: 200px; */
  font-size: 80px;
  color: white;
}

/* big buttons, no circle */
.flickity-prev-next-button {
  width: 100px;
  height: 100px;
  background: transparent;
}
/* arrow color */
.flickity-prev-next-button .arrow {
  fill: white;
}
.flickity-prev-next-button.no-svg {
  color: white;
}
.flickity-prev-next-button:hover {
  background: transparent;
}
/* hide disabled button */
.flickity-prev-next-button:disabled {
  display: none;
}
@keyframes fadeInUp {
  from { 
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}
.mbox{
// transform: translateX(30px);
// transition: 0.5s ease-in-out;
  // first we make all instances of this transparent. 
  opacity: 0; 
  // as we're using the same animation for each instance, call it here so we're not repeating it
  animation: fadeInUp 0.8s ease-in-out 0s forwards;
}
</style>


