<template>
  <div>
    <nav class="navbar navbar-light navbar-expand-md bg-faded justify-content-center">
      <!-- <img href="/" class="navbar-brand d-flex  mr-auto" src="../assets/bubble.png"/> -->

      <a href="/" class="navbar-brand d-flex w-50 mr-auto" style="font-size:30px;font-weight:bold;">
        <img src="../assets/bubble.png" />
        <span style="margin:auto 0px;">HUNG YI TING</span>
      </a>
      <button
        class="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#collapsingNavbar3"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="navbar-collapse collapse w-100" id="collapsingNavbar3">
        <ul class="nav navbar-nav ml-auto w-100 justify-content-end">
          <li class="nav-item">
            <router-link class="nav-link" to="/about">ABOUT</router-link>
          </li>
          <!-- <li class="nav-item">
                        <router-link class="nav-link"  to="/work" >WORK</router-link>
          </li>-->
          <li class="nav-item dropdown">
            <!-- <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        WORK
            </a>-->
            <router-link
              class="nav-link dropdown-toggle"
              to="#"
              role="button"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >WORK</router-link>

            <div class="dropdown-menu" aria-labelledby="navbarDropdown">
              <!-- <a class="dropdown-item" href="#">ART</a> -->
              <router-link class="dropdown-item nav-link" to="/art">ART</router-link>
              <!-- <div class="dropdown-divider"></div> -->
              <!-- <a class="dropdown-item" href="#">WEB</a> -->
              <router-link class="dropdown-item nav-link" to="/web">WEB</router-link>
              <!-- <a class="dropdown-item" href="#">APP</a> -->
              <router-link class="dropdown-item nav-link" to="/other">OTHER</router-link>
            </div>
          </li>
          <li class="nav-item">
            <router-link class="nav-link" to="/contact">CONTACT</router-link>
          </li>
        </ul>
      </div>
    </nav>
  </div>
</template>

<script>
import $ from "jquery";
$(document).ready(function() {
  $(".ml-auto .nav-item").bind("click", function(event) {
    $(".navbar-nav .nav-item").removeClass("active");
    $(this).addClass("active");
    // $(this).parent().a÷ddClass('active').siblings().removeClass('active');
  });

  var location = window.location.href;
  $(".navbar-nav .nav-item .nav-link").each(function() {
    if (location.indexOf(this.href) > -1) {
      $(this)
        .parent()
        .addClass("active");
    }
  });
});

export default {
  data() {
    return {};
  },
  methods: {
    refresh: function() {
      this.$router.go(0);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.active {
  border-bottom: 1px solid #000;
}
.dropdown-item.active, .dropdown-item:active{
  background-color: transparent;
}
</style>



