<template>
  <div class="container mbox">
    <div class="el-row">
      <div class="el-col el-col-8">
        <img src="../assets/avatar_square.png" class="pic" style="width:75%;" />
      </div>
      <div class="el-col el-col-12">
        <span class="text" style="font-weight:700;font-size:30px;">洪翊婷 HUNG YI TING</span>
        <br />
        <span class="text">
          目前就讀台大財金所，  於大學修習多項跨領域學程，具備多國語文能力並結合財經、數理、科技多元領域。此外參與程式培訓營，加強程式語言能力，希望未來能有機會結合自身金融專業，成為一流金融科技人才。
        </span>
      </div>
    </div>
    <div class="el-row mt-5">
      <div class="el-col el-col-12">
        <span class="text" style="font-weight:600;">EDUCATION</span>
        <br />
        <span class="text">
          國立政治大學 金融學系 2015-2019
          <br />國立臺灣大學 財務金融碩士 2019-2021
        </span>
        <br />
        <span class="text" style="font-weight:600;">EXTRACURRICULAR ACTIVITIES</span>
        <br />
        <span class="text">第四屆CMoney菁英軟體工程師戰鬥營 學員</span>
      </div>
      <div class="el-col el-col-12">
        <span class="text" style="font-weight:600;">SKILL</span>
        <br />
        <span class="text">網頁設計 | HTML, CSS, JavaScript, PHP, Vue.js</span>
        <br />
        <span class="text">程式語言 | Java, Python</span>
        <br />
        <span class="text">資料庫 | MongoDB, MySQL</span>
        <br />
        <span class="text">繪圖軟體 | PhotoImpact, Illustrator, Photoshop, SAI</span>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {};
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.container {
  margin: 30px 10%;
  line-height: 2.5rem;
}
.text {
  font-size: 20px;
  font-weight: 100;
  color: rgba(0, 0, 0, 0.7);
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}
.mbox {
  // transform: translateX(30px);
  // transition: 0.5s ease-in-out;
  // first we make all instances of this transparent.
  opacity: 0;
  // as we're using the same animation for each instance, call it here so we're not repeating it
  animation: fadeInUp 0.8s ease-in-out 0s forwards;
}
</style>



