import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import About from '@/components/About'
import Work from '@/components/Work'
import Contact from '@/components/Contact'
import Art from '@/components/Art.1'
import Web from '@/components/Web'
import Other from '@/components/Other'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'About',
      component: About
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/work',
      name: 'Work',
      component: Work
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact
    },
    {
      path: '/art',
      name: 'Art',
      component: Art
    },
    {
      path: '/web',
      name: 'Web',
      component: Web
    },
    {
      path: '/other',
      name: 'Other',
      component: Other
    },
  ]
})



